<template>
  <div class="blank">
    <div class="question-title">
      <span class="badge">{{required ? '必填' : '选填'}}</span>
      <span class="title">{{ title }}</span>
      <span class="description" v-if="description && description.replace(/\s/g, '')">（{{ description }}）</span>
    </div>
    <Form :model="model" :rules="validators">
      <FormItem prop="value">
        <xInput @on-blur="blur" v-model="model.value" @on-change="change" :readonly="readonly" :placeholder="placeholder" />
      </FormItem>
    </Form>
  </div>
</template>

<script>
import validate from '@/api/validate'

export default {
  name: 'lp-blank',
  props: {
    schemaKey: {
      type: String,
      require: true
    },
    title: {
      type: String,
      default: ''
    },
    description: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    additionalKeys: {
      type: Array
    },
    validate: {
      type: Boolean
    },
    readonly: {
      type: Boolean
    },
    readonlyData: {
      type: Array,
      default: () => []
    },
    required: {
      type: Boolean,
      default: false
    },
    schemaType: {
      type: String
    },
    apiType: {
      type: String
    },
    server: {
      type: Boolean,
      require: false
    }
  },
  data () {
    return {
      uuid: '00000000-0000-0000-0000-000000000011',
      model: {
        value: ''
      },
      validators: {
        value: [
          { required: this.required, message: '该项目为必填', trigger: 'blur' }
        ]
      }
    }
  },
  mounted () {
    this.model.value = this.value
    this.change(this.model.value, true)
    // this.$emit('validator', { key: this.schemaKey, validator: this.validateForm }) // 弹射验证器
  },
  methods: {
    change (val, first) {
      this.$emit('input', {
        schemaKey: this.schemaKey,
        value: val,
        server: this.server,
        first
      })
    },
    async blur () {
      if (this.additionalKeys && this.additionalKeys.length) {
        this.$emit('additional', {
          allowClose: true,
          additionalKeys: this.additionalKeys
        })
      }

      if (this.validate) {
        const data = await validate(this.schemaType, this.apiType, {
          projectId: this.$route.query.projectId,
          valueJson: '{"' + this.schemaKey + '":"' + this.currentValue + '"}'
        })

        if (data.response.success === false && data.response.msg) {
          this.$Message.error(data.response.msg)
        } else if (data.response.success === true && data.response.msg) {
          this.$Message.info(data.response.msg)
        }
      }
    },
    validateForm (success, failed) {
      return new Promise((resolve, reject) => {
        this.$refs.blankForm.validate((valid) => {
          if (valid) {
            resolve()
          } else {
            reject(new Error('必填'))
          }
        })
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
.blank
  padding 0.1rem
  margin auto
  display flex
  flex-direction column
  font-size 1rem
  width 100%
  .question-title
    text-align left
    margin-bottom 0.5rem
    .badge
      display inline-block
      color #ffffff
      background-color #f39938
      height 24px
      line-height 24px
      width 46px
      text-align center
      border-radius 12px
      font-size 13px
    .title
      font-size 14px
      font-weight bold
      margin-left 0.5rem
    .description
      font-size 13px
      color #666
      margin-left 0.5rem
</style>
